<template>
  <div class="whole-compra-container">
  <Menu/>
  <navBar class="nav"/>
    <div class="compra-container max-width">
            <div class="izquierda relative">
                <div class="flex whole-info">
                    <img :src="stage" alt="" class="stage">
                    <div class="info">
                    <router-link to="/taller/Illustration-Toolbox" class="volver flex">
                        <img src="../../assets/icons/flecha.svg" alt="">
                        <p>Anterior</p>
                    </router-link>
                    <h3>Illustration Toolbox</h3> 
                    <p class="descripcion">Un método para ilustrar</p>

                    <h1>2800 ARS</h1>

                    <div class="flex">
                            <img src="../../assets/icons/tick.svg" alt="" class="tick">
                            <p>Materiales incluídos</p>
                    </div>
                    <div class="flex">
                            <img src="../../assets/icons/tick.svg" alt="" class="tick">
                            <p>Proyecto final</p>
                    </div>
                    <div class="flex">
                            <img src="../../assets/icons/tick.svg" alt="" class="tick">
                            <p>Certificación de Haus</p>
                    </div>

                    <div class="tiempo flex">
                            <div class="flex">
                                <strong>Comienza</strong>
                                <p>14:10</p>
                            </div>
                            <div class="flex">
                                <strong>Duración</strong>
                                <p>3 clases</p>
                            </div>
                    </div>
                    </div>
                </div>
            </div>
            <div class="derecha relative">
                <div class="part1">
                    <h3>1. Ingresá tus datos</h3>
                    <form action="">
                        <div class="par flex">
                            <input type="text" id="name" @input="handleP1('#name','#nameLab')">
                            <label for="name" id="nameLab">NOMBRE</label>                    
                        </div>
                        <div class="par flex">
                            <input type="email" id="email" @input="handleP1('#email','#mailLab')">
                            <label for="email" id="mailLab">E-MAIL</label>
                        </div>
                        <a @click="translate">IR A PAGAR</a>
                    </form>
                </div>
                <div class="part2">
                    <h3>2. Ingresá los datos de tu tarjeta</h3>
                    <form action="">
                        <div class="par flex name">
                            <input type="text" id="cardName" @keyup="handleCardName('#nameLabel','#cardName','.name')"  tabindex="-1">
                            <label id="nameLabel" for="cardName">NOMBRE DEL TITULAR</label>
                            <img src="../../assets/icons/inputTick.svg" class="tick">
                        </div>
                        <div class="par flex num">
                            <input type="tel" id="cardNum" maxlength="16" @keyup="handleCardNum('#numLabel','#cardNum','.num')" tabindex="-1">
                            <label id="numLabel" for="cardNum">NÚMERO DE LA TARJETA</label>
                            <img src="../../assets/icons/inputTick.svg" class="tick">
                        </div>
                            <div class="short flex exp">
                                <div class="par flex">
                                    <input type="tel" id="cardExp" maxlength="5" @keyup="handleCardExp('#expLabel','#cardExp','.exp')" tabindex="-1">
                                    <label id="expLabel" for="cardExp">NÚM. EXP.</label>
                                    <img src="../../assets/icons/inputTick.svg" class="tick">
                                </div>
                                <div class="par flex cvv">
                                    <input type="tel" id="cardCVV" maxlength="3" @keyup="handleCardCvv('#cvvLabel','#cardCVV','.cvv')" tabindex="-1">
                                    <label id="cvvLabel" for="cardCVV">CVV</label>
                                    <img src="../../assets/icons/inputTick.svg" class="tick">
                                </div>    
                            </div>
                        
                        <div class="buttons-container flex">
                            <a @click="alerta">PAGÁ CON MERCADOPAGO</a>
                            <hr>
                            <a @click="finish">FINALIZAR PAGO</a>
                        </div>
                    </form>
                </div>
            </div>
    </div>
    <Footer class="footer"/>
    <div class="finalizar-container">
        <div class="info-container max-width">
                <img src="../../assets/taller/rayon.png" alt="" class="rayon">
                <img src="../../assets/recursos/stage3.svg" alt="" class="stage">
                <img src="../../assets/taller/title-final.png" alt="" class="titulillo">
                <h4>¡Gracias por formar parte de Haus! </h4>
                <p>Te enviamos un correo electrónico con todos los datos y tu factura de compra</p>
                <router-link class="main-button" to="/taller">VOLVER AL TALLER</router-link>
                <img class="ppal" src="../../assets/taller/imagen-final.png" alt="">
        </div>
    </div>
  </div>
</template>

<script>
import navBar from '../../components/navBar.vue'
import Menu from '../../components/menu.vue'
import Footer from '../../components/Footer.vue'

export default {
    name:'IluCompra',
    data(){
        return{
            stage:require('../../assets/recursos/stage1.svg')
        }
    },
    components:{
        Menu,
        navBar,
        Footer
    },
    methods:{
        translate(){
            let part1 = document.querySelector('.compra-container .part1');
            part1.style.transform = 'translateY(-200%)'
            
            let part2 = document.querySelector('.compra-container .part2');
            part2.style.transform = 'translateY(-79%)'
            this.stage = require('../../assets/recursos/stage2.svg')
        
        },
        finish(){
            let container = document.querySelector('.compra-container');
            container.style.top = '-100%'

            let finish = document.querySelector('.finalizar-container')
            finish.style.display = 'block'
            finish.style.top = '0'
        },
        alerta(){
            alert('Sección bajo construcción')
        },
        handleCardName(label,input,cont){
            let Label = document.querySelector(`${label}`)
            let inputVal = document.querySelector(`${input}`)
            let tick = document.querySelector(`${cont} .tick`)
            let regex = /[a-z]\s/
            if(inputVal.value.length > 0){
                Label.style.transform = 'translate(0%,-400%)'
                Label.style.fontSize = '.7rem'
            } else{
                Label.style.transform = 'translate(1%,-200%)'
                Label.style.fontSize = '.9rem'
            }

            if(inputVal.value.match(regex)){   
                tick.style.opacity = '1'
                
            } else{
                tick.style.opacity = '0'             
            }
        },
        handleCardNum(label,input,cont){
            let Label = document.querySelector(`${label}`)
            let inputVal = document.querySelector(`${input}`)
            let tick = document.querySelector(`${cont} .tick`)
            let regex = /^[0-9]{16,}$/

            if(inputVal.value.length > 0){
                Label.style.transform = 'translate(0%,-400%)'
                Label.style.fontSize = '.7rem'
            } else{
                Label.style.transform = 'translate(1%,-200%)'
                Label.style.fontSize = '.9rem'
            }

            if(inputVal.value.match(regex)){   
                tick.style.opacity = '1'
                
            } else{
                tick.style.opacity = '0'             
            }
        },
        handleCardExp(label,input,cont){
            let Label = document.querySelector(`${label}`)
            let inputVal = document.querySelector(`${input}`)
            let tick = document.querySelector(`${cont} .tick`)
            let regex = /([/][0-9])/

           if(inputVal.value.length > 0){
                Label.style.transform = 'translate(0%,-400%)'
                Label.style.fontSize = '.7rem'
            } else{
                Label.style.transform = 'translate(1%,-200%)'
                Label.style.fontSize = '.9rem'
            }

            if(inputVal.value.match(regex)){   
                tick.style.opacity = '1'
                
            } else{
                tick.style.opacity = '0'             
            }
        },
        handleCardCvv(label,input,cont){
            let Label = document.querySelector(`${label}`)
            let inputVal = document.querySelector(`${input}`)
            let tick = document.querySelector(`${cont} .tick`)
            let regex = /^[0-9]{3,}$/

            if(inputVal.value.length > 0){
                Label.style.transform = 'translate(0%,-400%)'
                Label.style.fontSize = '.7rem'
            } else{
                Label.style.transform = 'translate(1%,-200%)'
                Label.style.fontSize = '.9rem'
            }

            if(inputVal.value.match(regex)){   
                tick.style.opacity = '1'
                
            } else{
                tick.style.opacity = '0'             
            }
        },
        handleP1(p1Input,p1Label){
            let Label = document.querySelector(`${p1Label}`)
            let inputVal = document.querySelector(`${p1Input}`)

            if(inputVal.value.length > 0 ){
                Label.style.transform = 'translate(0%,-400%)'
                Label.style.fontSize = '.7rem'
            }
                
            } 
        }
    }

</script>

<style scoped>
.nav{
    position:absolute;
    z-index: 1000;
    min-width: 75rem;
    background:none;
}
