<script>
import Menu from '../components/menu.vue'
import navBar from '../components/navBar.vue'
import Footer from '../components/Footer.vue'

export default {
    name:'tienda',
    components:{
        Menu,
        navBar,
        Footer
    },
    data(){
        return{
            numProductos:0,
            productos:[
                
            ],
            total:null

        }
    },
    methods:{
        addProducto(name, price){
            this.numProductos++
            let producto = new Object()
            producto.nombre = name
            producto.precio = price

            this.productos.push(producto)
            let totalPrecio = this.productos.reduce((sum, objeto) => (typeof objeto.precio == "number" ? sum + objeto.precio : sum), 0);
            this.total = `$${totalPrecio}`

        },
        toggleLista(){
            let lista = document.querySelector('.lista-container')
            lista.style.transform = 'translateX(0%)'

        },
        hideLista(){
            let lista = document.querySelector('.lista-container')
            lista.style.transform = 'translateX(100%)'
        }
    }

}
</script>

